<template>
  <transition name="move">
    <div class="food-detail" ref="food" v-show="showFlag">
      <div class="food-content">
        <div class="image-header">
          <img :src="food.image" alt="">
          <div class="back" @click="hide"><i class="icon-arrow_lift"></i></div>
        </div>
        <div class="content">
          <h1 class="title">{{food.name}}</h1>
          <div class="detail">
            <span class="sell-count">月售{{food.sellCount}}份</span>
            <span class="rating">好评率{{food.rating}}%</span>
          </div>
          <div class="price">
            <span class="now">￥{{food.price}}</span>
            <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
          </div>
          <div class="control-wraper">
            <cart-control :food="food" @add="addCart"></cart-control>
          </div>
          <transition name="fade">
            <div class="buy" @click.stop.prevent="addFirst" v-show="!food.count || food.count === 0">加入购物车</div>
          </transition>
        </div>
        <split v-show="food.info"></split>
        <div class="info" v-show="food.info">
          <div class="title">商品信息</div>
          <div class="text">{{food.info}}</div>
        </div>
        <split></split>
        <div class="rating">
          <div class="title">商品评价</div>
          <rating-select @select="selectRating" @toggle="toggleContent" :ratings="food.ratings" :select-type="selectType" :only-content="onlyContent" :desc="desc"></rating-select>
          <div class="rating-wraper">
            <ul v-show="food.ratings && food.ratings.length">
              <li v-if="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
                <div class="user">
                  <span class="name">{{rating.username}}</span>
                  <img class="avatar" :src="rating.avatar" width="12" height="12" alt="">
                </div>
                <div class="time">{{rating.rateTime | formatDate}}</div>
                <p class="text">
                  <span :class="{'icon-thumb_up': rating.rateType===0,'icon-thumb_down': rating.rateType===1}"></span>{{rating.text}}
                </p>
              </li>
            </ul>
            <div class="no-rating" v-if="!food.ratings || !food.ratings.length">暂无评价</div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import Vue from "vue";
import BScroll from "better-scroll";
import {formatDate} from "../../common/js/date";
import split from "@/components/split/split";
import cartControl from "@/components/cartcontrol/cartcontrol";
import ratingSelect from "@/components/ratingselect/ratingselect";

const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;

export default {
  props: {
    food: {
      type: Object
    }
  },
  data() {
    return {
      showFlag: false,
      selectType: ALL,
      onlyContent: false,
      desc: {
        all: "全部",
        positive: "推荐",
        negative: "吐槽"
      }
    };
  },
  filters: {
    formatDate(time){
       let date = new Date(time);
       return formatDate(date, 'yyyy-MM-dd hh:mm');
    }
  },
  methods: {
    show() {
      this.showFlag = true;
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    hide() {
      this.showFlag = false;
    },
    addCart() {
      this.$emit("add", event.target);
    },
    addFirst(event) {
      if (!event._constructed) {
        return;
      }
      this.addCart();
      Vue.set(this.food, "count", 1);
    },
    selectRating(type) {
      this.selectType = type;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    },
    toggleContent(){
      this.onlyContent = !this.onlyContent;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    },
    needShow(type, text){
      if(this.onlyContent && !text){
        return false;
      }
      if(this.selectType === ALL){
        return true;
      }else{
        return type === this.selectType;
      }
    }
  },
  components: {
    split,
    cartControl,
    ratingSelect
  }
};
</script>

<style lang="less">
@import "../../common/less/mixin";
.food-detail {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
  &.move-enter-active,
  &.move-leave-active {
    opacity: 1;
    transform: translate3d(0, 0, 0);
    transition: all 0.2s ease;
  }
  &.move-enter,
  &.move-leave-to {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  .image-header {
    position: relative;
    padding-bottom: 100%;
    width: 100%;
    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
    .back {
      position: absolute;
      left: 0;
      top: 10px;
      .icon-arrow_lift {
        display: block;
        padding: 10px;
        font-size: 20px;
        color: #fff;
      }
    }
  }
  .content {
    position: relative;
    padding: 18px;
    .title {
      margin-bottom: 8px;
      font-size: 14px;
      font-weight: 700;
      line-height: 14px;
      color: rgb(7, 17, 27);
    }
    .detail {
      margin-bottom: 18px;
      height: 10px;
      line-height: 10px;
      font-size: 0;
      .sell-count,
      .rating {
        font-size: 10px;
        color: rgb(147, 153, 159);
      }
      .sell-count {
        margin-right: 12px;
      }
    }
    .price {
      line-height: 24px;
      .now {
        margin-right: 8px;
        font-size: 14px;
        font-weight: 700;
        color: rgb(240, 20, 20);
      }
      .old {
        text-decoration: line-through;
        font-size: 10px;
        font-weight: 700;
        color: rgb(147, 153, 159);
      }
    }
    .control-wraper {
      position: absolute;
      right: 12px;
      bottom: 12px;
    }
    .buy {
      position: absolute;
      right: 18px;
      bottom: 18px;
      z-index: 10;
      padding: 0 12px;
      height: 24px;
      line-height: 24px;
      font-size: 10px;
      border-radius: 12px;
      box-sizing: border-box;
      background: rgb(0, 160, 220);
      color: #fff;
      &.fade-enter-active,
      &.fade-leave-active {
        transition: opacity 0.5s;
      }
      &.fade-enter,
      &.fade-leave-to {
        opacity: 0;
      }
    }
  }
  .info {
    padding: 18px;
    .title {
      margin-bottom: 6px;
      line-height: 14px;
      font-size: 14px;
      color: rgb(7, 17, 27);
    }
    .text {
      padding: 0 8px;
      line-height: 24px;
      font-size: 12px;
      color: rgb(77, 85, 93);
    }
  }
  .rating {
    padding-top: 18px;
    .title {
      margin-left: 18px;
      line-height: 14px;
      font-size: 14px;
      color: rgb(7, 17, 27);
    }
  }
}
.rating-wraper {
  padding: 0 18px 18px;
  .rating-item {
    position: relative;
    padding: 16px 0;
    .border-1px(bottom, rgba(7,17,27,.1));
    &:last-child::after {
      display: none;
    }
    .user {
      position: absolute;
      right: 0;
      top: 16px;
      line-height: 12px;
      font-size: 0;
      .name {
        display: inline-block;
        vertical-align: top;
        margin-right: 6px;
        font-size: 10px;
        color: rgb(147, 153, 159);
      }
      .avatar {
        border-radius: 50%;
      }
    }
    .time {
      margin-bottom: 6px;
      line-height: 12px;
      font-size: 10px;
      color: rgb(147, 153, 159);
    }
    .text {
      line-height: 16px;
      font-size: 12px;
      color: rgb(7, 17, 27);
      .icon-thumb_up, .icon-thumb_down {
        margin-right: 4px;
        line-height: 16px;
        font-size: 12px;
      }
      .icon-thumb_up {
        color: rgb(0, 160, 220);
      }
      .icon-thumb_down {
        color: rgb(147, 153, 159);
      }
    }
  }
  .no-rating {
    padding: 16px 0;
    font-size: 12px;
    color: rgb(147, 153, 159);
  }
}
</style>



